<template>
  <div class="session-menu">
    
      <div class="container pt-5 pb-5">
        <div class="row">
          
          <div class="col-3 g-4" v-for="(item, index) in foodList" :key="index">
            <div class="card card-item">
              <img
                class="card-img-top h-50"
                :src="item.images[0].imageUrl"
                alt="Ảnh sản phẩm"
              />
              <div class="card-body">
                <h4 class="card-title">{{ item.foodName }}</h4>
                <h6 class="card-text"><b>Giá: </b>{{ item.price }} &#8363;</h6>
                <p class="card-text card-desc">
                  <b>Mô tả:</b>
                  {{ item.description }}
                </p>
                <p class="text-center mt-2 mb-0">
                  <router-link
                    :to="{
                      name: 'Detail',
                      params: { idFood: item.foodId },
                    }"
                    class="btn-card view-detail d-inline-block"
                    >Xem chi tiết</router-link>
                </p>
              </div>
            </div>
          </div>
        </div>
        <button
          @click="this.getNextFoodList"
          class="btn btn-success btn-view-more"
        >
          Xem Thêm...
        </button>
      </div>

    <div class="scroll-top text-white">
      <a href="#"><i class="scroll-top-icon bi bi-arrow-up-square-fill"></i></a>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import axios from "axios";
export default {
  created: function () {
    var id = this.$route.params.id;
    this.getFoodList(id);
  },
  mounted() {
    this.showScollTop();
  },
  computed: {
    ...mapGetters(["foodList"]),
  },
  methods: {
    ...mapMutations(["getFoodList", "pagination"]),
    ...mapActions(["getFoodList", "getNextFoodList", "addTocart"]),
    async getNextFoodList() {
      var id = this.$route.params.id;
      var lastItem = this.foodList.length - 1;
      var date = this.foodList[lastItem].createdAt;
      await axios
        .get("/cate/food/" + id + "/" + date + "/8")
        .then((response) => {
          var foods = response.data.data;
          foods.forEach((food) => {
            this.foodList.push(food);
            // commit('getFoodList', food)
          });
        });
    },
    showScollTop() {
      window.onscroll = function () {
        var btnScrolltop = document.querySelector(".scroll-top");
        if (
          document.documentElement.scrollTop >= 50 ||
          document.body.scrollTop >= 50
        ) {
          btnScrolltop.style.display = "block";
        } else {
          btnScrolltop.style.display = "none";
        }
      };
    },
}
}
</script>

